* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
}

.app .background {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url('/public/roka_jade.png') no-repeat center center/cover;
  width: 100vw;
  height: 90.57vw;
  max-height: 100vh;
  max-width: 110.3vh;
}

.app .background .title {
  position: absolute;
  top: 23%;
  left: 60%;
  width: 30%;
  height: 5%;
  font-size: 2.9vmin;
}

.app .background .time {
  position: absolute;
  top: 32%;
  left: 62%;
  width: 30%;
  height: 5%;
  font-size: 2.9vmin;
}